import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { hideModal } from "../../../actions/datamaster_action";
import {
  HiidenFiled,
  ReanderField,
  ReanderSelect,
} from "../../../components/helpers/field";
import { getData } from "../../../config/axios";
import ValidasiBaki from "../../../Validasi/ValidasiBaki.jsx";
import Loading from 'react-fullscreen-loading';
import { Gramasi } from "../../../Validasi/normalize";
const maptostate = (state) => {
  if (state.datamaster.ShowModalBaki !== null) {
    return {
      initialValues: {
        id: state.datamaster.ShowModalBaki.id,
        kode_gudang: state.datamaster.ShowModalBaki.kode_gudang,
        kode_baki: state.datamaster.ShowModalBaki.kode_baki,
        nama_baki: state.datamaster.ShowModalBaki.nama_baki,
        berat_baki: state.datamaster.ShowModalBaki.berat_baki,
        berat_bandrol: state.datamaster.ShowModalBaki.berat_bandrol,
      },
    };
  }
};
class FormModalBaki extends Component {
  constructor(props) {
    super(props);
    this.state = {
      aktif: false,
      listgroup: [],
      parameter_bandrol: "2301",
    };
  }

  componentDidMount() {
    getData("gudang/get/all/master")
      .then((res) => {
        this.setState({
          listgroup: res.data,
        });
      })
      .catch((err) => {
        console.log(err);
      });
    setTimeout(() => {
      this.props.isEdit
        ? document.getElementById("nama_baki").focus()
        : document.getElementById("kode_gudang").focus();
    }, 100);

    getData("parabandrol/get/all")
      .then((res) => {
        if (res.data.length === 0) {
          this.setState({
            parameter_bandrol: 0,
          });
          
        } else {
          this.setState({
            parameter_bandrol: res.data[0].berat_bandrol || 0,
          });
          
        }
      })
      .then(() =>
        this.props.change("berat_bandrol", this.state.parameter_bandrol)
      );
  }
  setFocus() {
    setTimeout(() => {
      document.getElementById("kode_baki").focus()
    }, 100);
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        {
          this.props.isEdit ? <>
             <Field
              name="id"
              component={HiidenFiled}
              type="hidden"
              label="Kode Baki"
              readOnly={this.props.isEdit}
            />
          </> : null
        }
        <div className="form-group">
          <Field
            tabIndex="1"
            id="kode_gudang"
            label="Kode Gudang"
            name="kode_gudang"
            options={this.state.listgroup
              .filter((list) => list.kode_gudang !== undefined)
              .map((list) => {
                let data = {
                  value: list.kode_gudang,
                  name: list.kode_gudang,
                };
                return data;
              })}
            value={this.props.kode_group}
            disabled={this.props.isEdit}
            onChange={()=>this.setFocus()}
            placeholder={"Silahkan Pilih Kode Gudang"}
            component={ReanderSelect}
          />
        </div>
        <Field
          tabIndex="2"
          id="kode_baki"
          name="kode_baki"
          component={ReanderField}
          type="text"
          label="Kode Baki"
          readOnly={this.props.isEdit}
          placeholder="Masukan Kode Baki"
        />
        <Field
          tabIndex="3"
          id="nama_baki"
          name="nama_baki"
          component={ReanderField}
          type="text"
          label="Nama Baki"
          placeholder="Masukan Nama Baki"
        />

        <Field
          tabIndex="4"
          name="berat_baki"
          component={ReanderField}
          normalize={Gramasi}
          type="text"
          label="Berat Baki"
          placeholder="Masukan Berat Baki"
        />

        <Field
          tabIndex="5"
          name="berat_bandrol"
          component={ReanderField}
          type="text"
          normalize={Gramasi}
          label="Berat Bandrol"
          onBlur={() => this.setState({ aktif: true })}
          placeholder="Masukan Berat Bandrol"
        />
        <div className="row">
          <div className="col-12 text-right">
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
            &nbsp;
            <button
              tabIndex="6"
              className={this.state.aktif ? "btn btn-primary" : "btn btn-white"}
              disabled={this.props.submitting}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

FormModalBaki = reduxForm({
  form: "ModalDataBaki",
  enableReinitialize: true,
  validate: ValidasiBaki,
})(FormModalBaki);
export default connect(maptostate, null)(FormModalBaki);
