import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";

import {
  ShowModalBaki,
  showModal,
  getDataBaki,
  hideModal,
} from "../../../actions/datamaster_action.jsx";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import {
  NotifSucces,
  ToastNotification,
} from "../../../components/helpers/notification.jsx";
import { deleteData, postData, putData } from "../../../config/axios.jsx";
import { reset } from "redux-form";
import { ErrorHandling } from "../../../components/helpers/function.jsx";
import Tabel from "../../../components/helpers/tabel.jsx";
import FormModalBaki from "./FormModalBaki.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";

const maptostate = (state) => {
  return {
    DataBaki: state.datamaster.getDataBaki,
    hideModal: state.datamaster.modalDialog,
  };
};

const hapusDataBaki = (kode,params, dispatch) => {
  Swal.fire({
    // title: "Anda Yakin !!",
    // text: "Ingin Menghapus Data Ini ?",
    html:
      "Apakah Anda Yakin Ingin " +
      "Menghapus " +
      "<h1><b>Kode Baki " +
      kode +
      "</b> ?</h1>",
    icon: "warning",
    position: "top-center",
    cancelButtonText: "Tidak",
    showCancelButton: true,
    confirmButtonText: "OK",
    showConfirmButton: true,
  }).then((result) => {
    if (result.isConfirmed) {
      deleteData("baki/delete/by-kode-baki/" + params).then(() => {
        NotifSucces("Data Berhasil Di Hapus").then(() =>
          dispatch(getDataBaki())
        );
      });
    }
  }).catch((err) => {
    ToastNotification("error", err.response.data)
  })
};
class MasterBaki extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      columns: [
        {
          dataField: "kode_gudang",
          text: "Kode Gudang ",
          sort: true,
        },
        {
          dataField: "kode_baki",
          text: "Kode Baki",
        },
        {
          dataField: "nama_baki",
          text: "Nama Baki",
        },
        {
          dataField: "berat_baki",
          text: "Berat Baki",
          formatter: (cell) => {
            return cell.toFixed(3);
          },
        },
        {
          dataField: "berat_bandrol",
          text: "Berat Bandrol",
          formatter: (cell) => {
            return cell.toFixed(3);
          },
        },

        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              id: row._id,
              kode_gudang: row.kode_gudang,
              kode_baki: row.kode_baki,
              nama_baki: row.nama_baki,
              berat_baki: row.berat_baki.toFixed(3),
              berat_bandrol: row.berat_bandrol.toFixed(3),
            };
            return (
              <div className="row text-center">
                <div className="col-12">
                  {row.kode_baki === "TITIPJUAL" ? null :
                    <>
                  <button
                    onClick={() => this.formEditBaki(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-edit"></i>
                  </button>
                  <button
                    onClick={() =>
                      hapusDataBaki(row.kode_baki,row._id, this.props.dispatch)
                    }
                    className="btn btn-danger"
                  >
                    <i className="fa fa-trash"></i>
                      </button>
                      </>
                    }
                </div>
              </div>
            );
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    this.props.dispatch(getDataBaki());
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
    let data_edit = {
      nama_baki: data.nama_baki,
      berat_bandrol: data.berat_bandrol,
      berat_baki: data.berat_baki,
    };
    let data_post = {
      kode_baki: data.kode_baki,
      nama_baki: data.nama_baki,
      kode_gudang: data.kode_gudang,
      berat_bandrol: data.berat_bandrol,
      berat_baki: data.berat_baki,
    };
    this.state.isEdit
      ? putData("baki/edit/by-kode-baki/" + data.id, data_edit)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Disimpan");
          })
          .then(() => {
            this.props.dispatch(getDataBaki());
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .then(() => {
            this.props.dispatch(reset("ModalDataBaki"));
          })
        .catch((error) => {
          ToastNotification("info", error.response === undefined ? "Terjadi Kesalahan Saat Megirim Data " : error.response.data)
          this.setState({ isLoading: false });
          })
      : postData("baki/add", data_post)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Disimpan");
          })
          .then(() => {
            this.props.dispatch(getDataBaki());
          })
          .then(() => {
            this.props.dispatch(reset("ModalDataBaki"));
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .catch((error) =>
            ErrorHandling(
              error,
              "baki/reactive/by-kode-baki/",
              // data.kode_baki,
              data_post,
              this.props.dispatch,
              getDataBaki(),
              hideModal(),
              "ModalDataBaki"
            ).then(() => {
              this.setState({ isLoading: false });
            })
          );
  }

  formEditBaki(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalBaki(dataEdit));
    this.setState({
      isEdit: true,
    });
  }
  ShowModalBaki() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalBaki(false));
    this.setState({
      isEdit: false,
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item active">Master Baki</li>
        </ol>
        <h1 className="page-header">Master Baki </h1>
        <Panel>
          <PanelHeader>Master Baki</PanelHeader>
          <br />

          {/* Master Baki */}
          <div className="container">
            <Tabel
              keyField="kode_baki"
              tambahData={true}
              handleClick={() => this.ShowModalBaki()}
              exportCsv={true}
              data={this.props.DataBaki}
              columns={this.state.columns}
              empty={this.props.DataBaki}
              textEmpty="Data Baki Kosong"
            />
          </div>
          <br />
          {/* End Master Baki */}
          <ModalGlobal
            size="P"
            title={this.state.isEdit ? "Edit Data Baki" : "Tambah Data Baki"}
            content={
              <FormModalBaki
                isLoading={this.state.isLoading}
                isEdit={this.state.isEdit}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            }
          />

          {/* End Tambah Master Baki  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(MasterBaki);
